.App .navbar {
  width: 100vw;
  background-color: black;
  height: 20vh;
  color: white;
  display: flex;
  justify-content: flex-end;
  padding-right: 30px;
  font-size: 20px;
  align-items: center;
}
.App .navbar .title {
  cursor: pointer;
}
.App .navbar .title:hover {
  color: grey;
  transition-duration: 1s;
}
.App .main {
  width: 100vw;
  height: 60vh;
  background-color: grey;
  display: flex;
}
.App .main .a {
  width: 33.3vw;
  height: 60vh;
  background-image: url("photos/a.png");
  background-repeat: no-repeat;
  background-size: 33vw 60vh;
  margin-right: 0.3vw;
  border: 1px solid white;
}
.App .main .b {
  width: 33.3vw;
  height: 60vh;
  background-image: url("photos/b.png");
  background-repeat: no-repeat;
  background-size: 33vw 60vh;
  margin-right: 0.3vw;
  border: 1px solid white;
}
.App .main .c {
  width: 33.3vw;
  height: 60vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url("photos/c.png");
  background-repeat: no-repeat;
  background-size: 33vw 60vh;
  border: 1px solid white;
}
.App .main .c .btn {
  background-color: #4eaf4e;
  color: white;
  padding: 10px;
  cursor: pointer;
}
.App .main .c .btn:hover {
  background-color: black;
  transition-duration: 1s;
}
.App footer {
  width: 100vw;
  height: 20vh;
  background-color: black;
  display: flex;
  color: white;
  justify-content: flex-start;
  align-items: center;
  padding-left: 30px;
  font-size: 15px;
}
@media only screen and (max-width: 600px) {
  .App .main {
    background-color: pink;
    flex-direction: column-reverse;
    height: 180vh;
  }
  .App .main .a {
    width: 100%;
    background-size: 100% 60vh;
  }
  .App .main .b {
    width: 100vw;
    background-size: 100% 60vh;
  }
  .App .main .c {
    width: 100vw;
    background-size: 100% 60vh;
  }
}
